
.food {
  position: fixed;
  left: 0;
  top: 0;
/*为什么要bottom？因为底部是留给购物车组件显示的，购物车组件高是48px*/
  bottom: 48px;
  background-color: #fff;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.move-enter-active,
.food.move-leave-active {
  -webkit-transition: all 0.5s linear;
  transition: all 0.5s linear;
}
.food.move-enter,
.food.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .img-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
/*padding设置百分比就是按照盒子的宽来计算的，所以此处设置100%的padding就会得到一个宽高相等的容器*/
}
.food .img-header img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.food .img-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .img-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-style: 20px;
  color: #fff;
}
.food .content {
  padding: 18px;
  position: relative;
}
.food .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food .content .detail {
  font-size: 0;
  margin-bottom: 18px;
  line-height: 10px;
  height: 10px;
}
.food .content .detail .sell-count,
.food .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food .content .detail .sell-count {
  margin-right: 12px;
}
.food .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food .content .price .now {
  margin-right: 18px;
  font-size: 14px;
  color: #f01414;
}
.food .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.food .content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 30;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background-color: #00a0dc;
  opacity: 1;
}
.food .content .buy.fade-enter-active,
.food .content .buy.fade-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.food .content .buy.fade-enter,
.food .content .buy.fade-leave-active {
  opacity: 0;
}
.food .info {
  padding: 18px;
}
.food .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food .info .text {
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
  line-height: 24px;
  margin: 0 8px;
}
.food .ratings {
  padding-top: 18px;
}
.food .ratings .title {
  margin-left: 18px;
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food .ratings .rating-wrapper {
  padding: 0 18px;
}
.food .ratings .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food .ratings .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.food .ratings .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
}
.food .ratings .rating-wrapper .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  margin-right: 6px;
}
.food .ratings .rating-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}
.food .ratings .rating-wrapper .rating-item .time {
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.food .ratings .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food .ratings .rating-wrapper .rating-item .text .icon-thumb_up,
.food .ratings .rating-wrapper .rating-item .text .icon-thumb_down {
  margin-right: 4px;
  line-height: 16px;
  font-size: 12px;
}
.food .ratings .rating-wrapper .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.food .ratings .rating-wrapper .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.food .ratings .rating-wrapper .on-ratings {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


