
.carcontrol {
  font-size: 0;
}
.carcontrol .cart-decrease {
  display: inline-block;
/*为什么要加padding？为了UI美观设计的时候是没有那么大的，但是为了用户体验是用户能够更加方便的点到这些按钮，所以在不增大显示样式的情况下通过增加padding来增加用户可点击的区域*/
  padding: 6px;
  opacity: 1;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
/*为什么要用translate3d ？？因为这个属性可以给开启一个硬件加速效果，让动画更为流畅一点*/
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.carcontrol .cart-decrease .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.carcontrol .cart-decrease.move-enter-active,
.carcontrol .cart-decrease.move-leave-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.carcontrol .cart-decrease.move-enter,
.carcontrol .cart-decrease.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0);
          transform: translate3d(24px, 0, 0);
}
.carcontrol .cart-decrease.move-enter .inner,
.carcontrol .cart-decrease.move-leave-active .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.carcontrol .cart-count {
  display: inline-block;
  padding-top: 6px;
  font-size: 10px;
  line-height: 24px;
  text-align: center;
  vertical-align: top;
  color: #93999f;
}
.carcontrol .cart-add {
  display: inline-block;
  padding: 6px;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}


